import React from 'react';
import "./styles/PageHeader.css";
import gradient from "../components/Images/line2.png";

const PageHeader = (props) => {
    return (
        <div>
            <div className="container">
                <h1>{props.title}</h1> 
                <img src={gradient} alt="divider" />
        </div> 
    </div>
    )
}

export default PageHeader; 

