import React from 'react';
import Row from "../components/Row";
import Card from "../components/Card";
import date from "../components/Images/date.png";
import road from "../components/Images/cycling.png";
import photos from "../components/Images/photos.png"; 
import edb from "../components/Images/edb3.jpg"; 
import weather from "../components/Images/weather.jpg";
import note from "../components/Images/note3.jpg"; 
import PageHeader from "../components/PageHeader";
import ScrollToTop from "../components/ScrollToTop"; 
import "./styles/Projects.css";

const Projects = () => {
    return (
        <div>
            <div className="projects">
                <PageHeader title="Highlighted Projects"/>
                <div className="projects-text1">
                    <p>Through bootcamp training, I have completed projects and assignments covering HTML5, CSS3, JavaScript, jQuery,
                    Express.js, React.js, Node.js, progressive web apps, agile methodology, database theory, MongoDB, MySQL,
                    Git, as well as utilizing frameworks such as Bootstrap and W3CSS to build user interfaces.</p>
                    <p>Link to each of my group projects below.  For each, my responsibility included wireframe, UI
                    and front end development. I developed the concepts for RoadRoutes and PhotoScribe.</p>
                </div>
                <div className="project-cards">
                    <Row className="card-deck">
                        <Card className="card">
                            <img src={date} alt="date night thumbnail" className="card-img-top" />
                            <div className="card-text">
                                <h5>Date Night</h5>
                            </div>
                            <a href="https://tiffcrockett.github.io/dinner-movie-app/" target="_blank" className="text-secondary">View</a>
                            <a href="https://github.com/tiffcrockett/dinner-movie-app" target="_blank" className="text-secondary">Github</a>
                        </Card>
                        <Card className="card">
                            <img src={road} alt="road routes thumbnail" className="card-img-top" />
                            <div className="card-text">
                                <h5>RoadRoutes</h5>
                            </div>
                            <a href="https://intense-meadow-14721.herokuapp.com/" target="_blank" className="text-secondary">View</a> 
                            <a href="https://github.com/tiffcrockett/roadroutes" target="_blank" className="text-secondary">Github</a>
                        </Card>
                        <Card className="card">
                            <img src={photos} alt="photoscribe thumbnail" className="card-img-top" />
                            <div className="card-text">
                                <h5>PhotoScribe</h5>
                            </div>
                            <a href="https://photoscribe.herokuapp.com/" target="_blank" className="text-secondary">View</a>
                            <a href="https://github.com/jkriese12/photoscribe" target="_blank" className="text-secondary" >Github</a>
                        </Card>
                    </Row>
                    <div className="projects-text2">
                    <p> Here are links to a few other completed projects.</p>
                </div>
                <div className="projects-cards2">
                    <Row className="card-deck">
                        <Card className="card">
                            <img src={edb} alt="date night thumbnail" className="card-img-top" />
                            <div className="card-text">
                                <h6>Eat-Da-Burger</h6>
                            </div>
                            <a href="https://shrouded-beach-35226.herokuapp.com/" target="_blank" className="text-secondary">View</a> 
                            <a href="https://github.com/tiffcrockett/eat-da-burger" target="_blank" className="text-secondary">Github</a>
                        </Card>
                        <Card className="card">
                            <img src={weather} alt="road routes thumbnail" className="card-img-top" />
                            <div className="card-text">
                                <h6>Weather</h6>
                            </div> 
                            <a href="https://tiffcrockett.github.io/weather-dashboard/" target="_blank" className="text-secondary">View</a>
                            <a href="https://github.com/tiffcrockett/weather-dashboard" target="_blank" className="text-secondary">Github</a>
                        </Card>
                        <Card className="card">
                            <img src={note} alt="photoscribe thumbnail" className="card-img-top" />
                            <div className="card-text">
                                <h6>Note Taker</h6>
                            </div>
                            <a href="https://safe-harbor-26741.herokuapp.com/" target="_blank" className="text-secondary" >View</a> 
                            <a href="https://github.com/tiffcrockett/note-taker" target="_blank" className="text-secondary" >Github</a>
                        </Card>
                    </Row>  
                </div>
            </div> 
            <ScrollToTop />
        </div>
       
    </div>



    )
}

export default Projects;
